<template>
  <div class="review-room-card">
    <div class="review-room-card-icon">
      <i class="iconfont icon-zhuozi"></i>
    </div>
    <div class="review-room-card-text">
      <div class="review-room-card-text-title">{{card.name}}</div>
      <div class="review-room-card-text-time">开放时间：{{card.time}}</div>
    </div>
    <div
      class="review-room-card-status"
      :class="{'green-bg':card.status,'red-bg':!card.status}"
      v-if="status"
    >{{card.status?"开":"关"}}</div>
  </div>
</template>
<script>
export default {
  props: {
    status: {
      type: Boolean,
      default: false
    },
    card: Object
  }
};
</script>
<style lang="less" scoped>
.review-room-card {
  width: 212px;
  padding: 12px 8px;
  border: 1px solid #dae1e9;
  border-radius: 5px;
  margin-right: 18px;
  margin-bottom: 18px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  overflow: hidden;
  user-select: none;
  &-icon {
    height: 50px;
    line-height: 50px;
    .iconfont {
      font-size: 2.5rem;
      color: #2a9efc;
    }
  }
  &-text {
    text-align: left;
    &-time {
      margin-top: 8px;
      color: #999999;
      font-size: 0.8rem;
    }
  }
  &-status {
    position: absolute;
    top: 0;
    right: 0;
    padding: 2px 5px;
    font-size: 0.8rem;
    border-bottom-left-radius: 5px;
    color: #fff;
  }
}
</style>



