<template>
  <div class="personnal-detail-wrap">
    <div class="personnal-detail-wrap-title">自习室列表</div>
    <div>
      <review-room v-for="(c,i) in rooms" :key="i" status :card="c" class="fl"/>
      <div class="clear"></div>
    </div>
    <bottom-pager :total="total" @change="pageChange"/>
  </div>
</template>
<script>
import reviewRoom from "../../../components/personal/reviewRoomCard";
import bottomPager from "../../../components/personal/bottomPager";
export default {
  components: {
    reviewRoom,
    bottomPager
  },
  data() {
    return {
      rooms: [
        {
          status: 0,
          name: "永亮楼203",
          time: "14:00-21:00"
        },
        {
          status: 0,
          name: "永亮楼203",
          time: "14:00-21:00"
        },
        {
          status: 1,
          name: "永亮楼203",
          time: "14:00-21:00"
        },
        {
          status: 0,
          name: "永亮楼203",
          time: "14:00-21:00"
        }
      ],
      total: 0
    };
  },
  created() {
    this.total = this.rooms.length;
  },
  methods: {
    pageChange(page) {}
  }
};
</script>
<style lang="less" scoped>
</style>



